import { GetAllPayments } from "@store/payment/thunk";
import { withAuth, NextRequestWithAuth } from "next-auth/middleware";
import { NextResponse, NextRequest } from "next/server";

export default withAuth(
  function middleware(request: NextRequestWithAuth) {
    // ** exclude the auth routes
    // console.log(request.nextUrl.pathname, "running middleware");
   
    if (
      request.nextUrl.pathname.startsWith("/api") ||
      request.nextUrl.pathname.startsWith("/partner/auth/signin") ||
      request.nextUrl.pathname.startsWith("/admin/auth/signin")
    ) {
      return NextResponse.next();
    } else {
      if (
        request.nextUrl.pathname.startsWith("/partner") &&
        request.nextauth.token?.role !== "partner"
      ) {
        return NextResponse.rewrite(new URL("/denied", request.url));
      }

      if (
        request.nextUrl.pathname.startsWith("/admin") &&
        request.nextauth.token?.role !== "admin"
      ) {
        return NextResponse.rewrite(new URL("/denied", request.url));
      }

      if (
        request.nextUrl.pathname.startsWith("/dashboard") &&
        request.nextauth.token?.role !== "user"
      ) {
        return NextResponse.rewrite(new URL("/denied", request.url));
      }
    }
  },
  {
    callbacks: {
      authorized: ({ req, token }) =>
        req.nextUrl.pathname === "/partner/auth/signin" ||
        req.nextUrl.pathname === "/admin/auth/signin" ||
        req.nextUrl.pathname === "/auth/signin" ||
        req.nextUrl.pathname === "/" ||
        // req.nextUrl.pathname === "/search" ||
        !!token,
    },
  }
);

export const config = {
  matcher: [
    // "/((?!api|_next/static|favicon.ico).*)",
    "/admin/((?!auth|signin|signup|forgot-password).*)",
    "/partner/((?!auth|signin|signup|forgot-password).*)",
    "/dashboard/:path*",
  ],
};
